<template>
	<div class="pageWrap" :style='"min-height:"+bgWrapHeight+"px;"'>
        <div class="orderDetailWrap">
            <div class="whiteBox" style="padding-top: .14rem; padding-bottom: .23rem;">
                <div class="scrollBox">
                    <div class="scrollTitle">Master of Yesterday</div>
                    <div v-swiper:mySwiper="swiperOption" class='mySwiper'>
                        <div class="swiper-wrapper">
                            <div class="swiper-slide" v-for="(item,index) in masterList" :key="index">
                                <div class="logoBox">
                                    <img class='userLogoImg' :src="item.avatarUrl" alt="">
                                </div>
                                <div class="userName">{{item.nickName}}</div>
                                <div class="earnNumber">{{'Earned: '+moneySymbol+item.earn}}</div>
                            </div>
                            
                        </div>
                    </div>
                </div>
            </div>
            <div class="borderBox"></div>
            <div class="whiteBox" style="padding: .18rem .31rem; ">
                <div class="whiteBoxTit flexBetween">
                    <span>Featured videos</span>
                    <div class="refreshBox" @click="clickRefresh()">
                        <template v-if="refreshTime == 0">
                            <img class='refreshIcon' src="../assets/images/blue_refresh.png" alt="">
                            <span class='refreshText'>Refresh</span>
                        </template>
                        <template v-else>
                            <img class='refreshIcon' src="../assets/images/hui_refresh.png" alt="">
                            <span class='refreshText' style="color:#BAC3CC;">{{refreshTime}}</span>
                        </template>
                    </div>
                </div>
                <div class="recentlyVideoBox flexStart">
                    <div class="recentlyItem" @click="goVideoDetail(item)" v-for="(item,index) in videoList" :key="index">
                        <div class="recentlyImgBox">
                            <div class="eyeBox flexBetween">
                                <div class="eyeLeft flexStart">
                                    <img class='eyeIcon' src="../assets/images/whiteEye.png" alt="">
                                    <p class="eyeNumber">{{item.totalView}}</p>
                                </div>
                                <div class="eyeRight">{{'+'+moneySymbol+parseFloat((item.totalEarn/1).toFixed(fixNumber))}}</div>
                            </div>
                            <van-image
                                class='recentlyImg'
                                :src="item.coverUrl"
                                fit="contain"
                            />
                        </div>
                        <div class="recentlyUserBox flexStart">
                            <img class='recentlyUserLogo' :src="item.poster.avatarUrl ? item.poster.avatarUrl : require('../assets/images/homeUserIcon2.png')" alt="">
                            <p class="recentlyUserName">{{item.poster.name}}</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <van-tabbar route fixed active-color='#38ADFF' inactive-color='#BAC3CC'>
			<van-tabbar-item replace :to="'/Home/'+VHinvite_code" :icon="require('../assets/images/homeNormalIcon.png')">Home</van-tabbar-item>
			<van-tabbar-item replace :to="'/Video/'+VHinvite_code" :icon="require('../assets/images/videoActiveIcon.png')">Video</van-tabbar-item>
			<van-tabbar-item replace :to="'/Libary/'+VHinvite_code" :icon="require('../assets/images/libaryNormalIcon.png')">Library</van-tabbar-item>
			<van-tabbar-item replace :to="'/Team/'+VHinvite_code" :icon="require('../assets/images/teamNormalIcon.png')">Team</van-tabbar-item>
			<van-tabbar-item replace :to="'/Account/'+VHinvite_code" :icon="require('../assets/images/accountNormalIcon.png')">Account</van-tabbar-item>
		</van-tabbar>
	</div>
</template>
<script>
	import https from '../api/https'
	export default {
		name: 'Video',
		data: function(){
			return {
				bgWrapHeight: 200,
                moneySymbol: https.moneySymbol,
                fixNumber: https.fixNumber,
                swiperOption: {
					loop: true,
					autoplay: {
						delay: 5000,
						stopOnLastSlide: false,
						disableOnInteraction: false,
					},
                    slidesPerView: 'auto'
				},
                showLoading: true,
                videoList: [],
                refresh: '',
                masterList: [],
                timer: null,
			}
		},
		created(){
			this.getData()

            if(this.$route.params.VHinvite_code && this.$route.params.VHinvite_code != 'undefined') {
				localStorage.setItem('VHinvite_code',this.$route.params.VHinvite_code)
				this.VHinvite_code = this.$route.params.VHinvite_code
			}else if(localStorage.getItem('VHinvite_code') && localStorage.getItem('VHinvite_code') != 'undefined') {
				this.VHinvite_code = localStorage.getItem('VHinvite_code')
			}else {
                this.VHinvite_code = ''
				localStorage.setItem('VHinvite_code','')
            }
		},
		mounted: function () {
			let viwePortHeight = document.documentElement.clientHeight || document.body.clientHeight
			this.bgWrapHeight = viwePortHeight
		},
		methods: {
			getData: function(){
				this.showLoading = true
				setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
				https.fetchGet('/api/video/video').then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
                        this.masterList = res.data.master
						this.videoList = res.data.featured
                        this.refresh = res.data.refresh
                        
                        if(this.refresh/1 > 0) {
                            this.timer = setInterval(() => {
                                if(this.refresh/1 > 0) {
                                   this.refresh = (this.refresh/1) - 1
                                }else  {
                                    clearInterval(this.timer)
                                }
                            }, 1000);
                        }
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getData()
					}
				})
			},
            clickRefresh(){
                if(!this.refreshTime) {
                    this.refreshVideo()
                }
            },
            refreshVideo: function(){
				this.showLoading = true
				setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
				https.fetchGet('/api/video/video/refresh').then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
						this.videoList = res.data.featured
                        this.refresh = res.data.refresh
                        if(this.refresh/1 > 0) {
                            this.timer = setInterval(() => {
                                if(this.refresh/1 > 0) {
                                   this.refresh = (this.refresh/1) - 1
                                }else  {
                                    clearInterval(this.timer)
                                }
                            }, 1000);
                        }
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getData()
					}
				})
			},
            selectClassType(item){
                this.selectClassTypeId = item.id
            },
            goVideoDetail(item){
                this.showLoading = true
				setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    id: item.id
                }
				https.fetchGet('/api/video/item',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
                        localStorage.setItem('videoData',JSON.stringify(res.data))
                        this.goPage('/recommendVideoDetail')
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else if(res.code == -8) {
						this.errorPageTitle = 'Information has expired'
						this.errorPageScript = 'This information is no longer valid'
						this.errorPageBtnText = 'Home'
						this.errorPageActive = true
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
                    this.showLoading = false
                    this.$toast({
                        message: https.NetworkErrorText,
                        forbidClick: true,
                        icon: require('../assets/images/errorIcon.png'),
                        duration: 2000,
                        className: 'resultToast'
                    });
				})
                
                
            },
			goPage(page){
                if(this.VHinvite_code) {
					page = page +'/'+ this.VHinvite_code
				}
				this.$router.push(page)
			},
			goBack(){
				this.$router.go(-1)
			},
		},
        computed: {
            refreshTime(){
                console.log(this.refresh)
                if(this.refresh/1 > 0) {
                    console.log(222)
                    var sec = parseInt(this.refresh);
                    var min= 0;
                    var hour= 0;
                    if(sec > 60) {
                        min= parseInt(sec/60);
                        sec = parseInt(sec%60);
                        if(min> 60) {
                            hour= parseInt(min/60);
                            min= parseInt(min%60);
                        }
                    }
                    var result = `${hour.toString().padStart(2,'0')}:${min.toString().padStart(2,'0')}:${sec.toString().padStart(2,'0')}`
                    return result;
                }else {
                    console.log(111)
                    return 0
                }
            }
        },
        destroyed(){
            if(this.timer) {
                clearInterval(this.timer)
            }
        }
	}
</script>
